.App {
  display: flex;
  background: #dcdcdc;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}

.App-header {
  display: none;
  background-color: #262626;
  overflow: visible;
  width: 100%;
  text-align: center;
  color: white;
}
.Message-content {
	width: 100%;
	margin: 0 10px;
}

.Messages-list {
  padding: 20px 0;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  background-color: #dcdcdc;
  list-style: none;
  padding-left: 0;
  flex-grow: 1;
  overflow: auto;
}

.Messages-message {
  display: flex;
}

.Messages-message.currentMember {
  /*justify-content: flex-end;*/
  flex-direction: row-reverse;
  text-align: right;
}

.Message-content {
  display: inline-block;
}

.currentMember > .Message-content {
  align-items: flex-end;
}

.Messages-message > .avatar {
  height: 31px;
  width: 33px;
  border-radius: 50%;
  //display: inline-block;
  display: none;
  margin: 0 7px -10px;
}

.Message-content > .username {
  display: block;
  color: gray;
  font-size: 14px;
  padding-bottom: 4px;
}

.Message-content > .text {
  padding: 7px 12px 7px 9px;
  min-width: 5%;
  margin: 0;
  border-radius: 7px;
  background-color: white;
  display: inline-block;
  line-height: 9px;
  font-family: monospace;
  font-size: 14px;
}

.currentMember > .Message-content .text {
  background-color: #C8C8C8;
}

form {
  display: flex;
  width: 100%;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto 40px;
}

input {
  padding: 5px;
  font-size: 16px;
  border-radius: 16px;
  border: 1px solid #C8C8C8;
  flex-grow: 1;
  margin: 5px 0;
}

button {
  padding: 5px 10px;
  font-size: 16px;
  background-color: #C8C8C8;
  color: white;
  border: none;
  border-radius: 8px;
  margin: 5px 0 5px 10px;
}
